import React from "react"

class Form extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      plumbingIssue: "",
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  handleChange(event) {
    this.setState({ plumbingIssue: event.target.value })
  }
  handleSubmit(event) {
    alert(this.state.plumbingIssue)
    event.preventDefault()
  }
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <h2 style={{ textAlign: "center" }}>
          What type of plumbing project is this?
        </h2>
        <div className="inputField">
          <input
            type="checkbox"
            id="emergency"
            value="emergency"
            name="plumbingIssue"
          ></input>
          <label htmlFor="emergency">Emergency Plumbing</label>
        </div>
        <div className="inputField">
          <input
            type="checkbox"
            id="leak"
            value="leak"
            name="plumbingIssue"
          ></input>
          <label htmlFor="leak">Leak Detection &amp; Repair</label>
        </div>
        <div className="inputField">
          <input
            type="checkbox"
            id="water"
            value="water"
            name="plumbingIssue"
          ></input>
          <label htmlFor="water">Water Heater Inpection</label>
        </div>
        <div className="inputField">
          <input
            type="checkbox"
            id="drain"
            value="drain"
            name="plumbingIssue"
          ></input>
          <label htmlFor="drain">Drain Cleaning</label>
        </div>
        <div className="inputField">
          <input
            type="checkbox"
            id="nodig"
            value="nodig"
            name="plumbingIssue"
          ></input>
          <label htmlFor="nodig">No Dig Relining</label>
        </div>
        <div className="inputField">
          <input
            type="checkbox"
            id="other"
            value="other"
            name="plumbingIssue"
          ></input>
          <label htmlFor="other">Other</label>
        </div>
        <div className="inputField submitField">
          <input
            class="btn"
            type="submit"
            id="submit"
            value="Find My Plumber"
          ></input>
        </div>
      </form>
    )
  }
}

export default Form
